body{
  background-color : blue;
}

.form{
  background-color: rgb(51, 170, 206);
  width: 800px;
  margin: 100px auto;
  padding: 50px;
}

#to-do-tasks input{
  color: white;
  background-color:rgb(102, 49, 187);
  font-size: 18pt;
  height: 50px;
  width: 600px;
  margin: 25px;
  border-radius: 8px;
  text-emphasis-color: white;
}

#to-do-tasks input::placeholder{
  color: white;
}

#to-do-tasks button {
  background-color:rgba(133, 5, 133, 0.678);
  color: white;
  height: 30px;
  width: 80px;
  border-radius: 8px;
}